.description-box{
    margin: 120px 170px;
}
.description-box-nav{
    display: flex;
}
.description-box-nav-box{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    font-weight: 600;
    width: 171px;
    height: 70px;
    border: 1px solid #d0d0d0;
}
.description-box-nav-box .fade{
    background: #fbfbfb;
    color: #555;
}
.description-box-description{
    display: flex;
    flex-direction: column;
    gap: 25px;
    border: 1px solid #d0d0d0;
    padding: 48px;
    padding-bottom: 70px;
}
@media (max-width:1280px){
    .description-box{
        margin: 60px;
    }
}
@media (max-width:800px){
    .description-box{
        margin: 60px 20px;
    }
    .description-box-description{
        font-size: 14px;
    }
 }